.Contact{
    padding: 10vh 5vw;
}

// contact box primary container
.contact-box-container{
   padding-top: 5vh;
   display: flex;
   justify-content: center;
}

// social icons container
.social-icons{
    display: flex;
    column-gap: .35cm;
}
// social icons
.social-icon{
    width: .8cm;
    height: .8cm;
}

.sub-search{
    color: rgb(184, 183, 183);
}
// contact box container
.contact-container{
    border: .1px solid rgb(50, 49, 49);
    display: flex;
    flex-direction: column;
    align-items: center;
    width: fit-content;
    height: auto;
    padding: .8cm;
    background-color: #1e1e1e;
    border-radius: 3px;
	box-shadow: rgba(122, 121, 121, 0.16) 0px 2px 3px, rgba(61, 60, 60, 0.23) 0px 2px 3px;
}

// contact form
.contact-form{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: .5cm;
}

// inputs
.contact-inputs{
    width: 7cm;
    background-color: rgba(203, 237, 251, 0.931);
    border: none;
    font-size: 16px;
    padding: .2cm;
    border-radius: 5px;
	box-shadow: rgba(122, 121, 121, 0.16) 0px 2px 3px, rgba(61, 60, 60, 0.23) 0px 2px 3px;
    font-family: roboto;

}

// contact form submit button
.submit-contact{
    background-color: rgb(183, 117, 246);
    font-size: 18px;
    border: none;
    border-radius: 5px;
    padding: .1cm .2cm;
    cursor: pointer;
}
.submit-contact:hover{
    background-color: rgb(171, 90, 246);

}

.email-at{
    color: rgb(197, 196, 196);
}