body{
    background-color: #16161d;
}
.App{
    padding: 0vw 5vw;
}
p{
    margin: 0px 0px;
}
h1,h2,li{
    margin: 0px 0px;
}
a{
  text-decoration: none;
}

::-webkit-scrollbar {
    width: 8px;
    height: 0px;
    transition: .3s;
  }
  
  
  
  
  ::-webkit-scrollbar-track {
    background: #ffffff; 
  }
   
  
  ::-webkit-scrollbar-thumb {
    background: rgb(98, 98, 98); 
  }
  
  
  ::-webkit-scrollbar-thumb:hover {
    background: rgb(101, 100, 101)  ; 
  }
  