// Primary container
.Projects{
    padding-top: 10vh ;
}

// projects container
.projects-container{
    padding: 5vh 0vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: .6cm;
    row-gap: .6cm;
}

// project
.project{
    border: .1px solid rgb(50, 49, 49);

    width: 10cm;
    height: auto;
    padding: 0.5cm;
    background-color: #1e1e1e;
    border-radius: 3px;
	box-shadow: rgba(122, 121, 121, 0.16) 0px 2px 3px, rgba(61, 60, 60, 0.23) 0px 2px 3px;
    
}

// project logo and name container
.project-logo-container{
    display: flex;
    align-items: center;
    column-gap: .3cm;
}

.project-year{
    padding-left: 2.3cm;
    color: blueviolet;
    font-size: 18px;
}

.project-logo{
    width: 2cm;
}

.project-name{
    font-size: 33px;
    letter-spacing: .025cm;
    color: rgba(197, 193, 193, 0.951);
    font-family: bree serif;

}

.project-tagline{
    font-size: 22px;
    letter-spacing: .02cm;
    color: rgb(146, 144, 144);
    font-family: raleway;
}

// container of button redirect to source code of project
.code{
    display: flex;
    align-items: baseline;
    column-gap: .08cm;
    border: .5px solid rgb(63, 63, 63);
    width: fit-content;
    padding: 1px 5px;
    border-radius: 3px;
    // background-color: #16161d;
    background-color: #2c2b2b;

	box-shadow: rgba(122, 121, 121, 0.16) 0px 2px 3px, rgba(61, 60, 60, 0.23) 0px 2px 3px;
    

}

// primary container of buttons
.project-buttons{
    margin-left: 2.3cm;
    display: flex;
    align-items: center;
    column-gap: .8cm;
    
}

// button redirect to source code of project
.code-link{
    color: rgb(239, 90, 60);
    cursor: pointer;
}
.code-link:hover{
    text-decoration: underline;

}

.star-link{
    color: rgb(17, 172, 11);
}

.demo-link{
    color: rgba(110, 144, 245, 0.921);

}
// buttons redirect icon
.pred{
    width: .25cm;
}