.Navbar{
    padding-top: .3cm;
}

// logo
.logo {
    width: 1.3cm;
}

// navigation bar's left division
.left-nav {
    display: flex;
    column-gap: .2cm;
    align-items: center;
}

// my name
.name{
    color: rgb(164, 161, 161);
    font-family: raleway;
    font-weight: 500;
    font-size: 33px;
    letter-spacing: .015cm;
}
.name:hover{
    color: rgb(243, 241, 241);
}
// navigation bar's menu item
.menuItem{
    color: rgb(182, 177, 177);
    cursor: pointer;
}
.menuItem:hover{
    color: rgb(243, 241, 241);
}
// navigation menu
.menu{
    display: flex;
    align-items: center;
    column-gap: .5cm;
}

// navigation menu container
.nav-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 0px;
    border-bottom: 1px solid rgb(63, 62, 62);
}

// link of nav menu to component
.link-nav{
    text-decoration: none;
    margin: 0px 0px;
}