// primary container
.Skills{
    padding-top: 10vh;
}

// skills container
.skills-container{
    padding-top: 5vh;
    display: flex;
    justify-content: center;
    column-gap: .6cm;
    row-gap: .6cm;
    flex-wrap: wrap;
}
// skill card
.skill{
    border: .5px solid #636367;
    width: 4cm;
    padding: .5cm;
    background-color: #1e1e1e;
    border-radius: 3px;
	box-shadow: rgba(122, 121, 121, 0.16) 0px 2px 3px, rgba(61, 60, 60, 0.23) 0px 2px 3px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: .1px solid rgb(50, 49, 49);
}

// image of skill
.skill-img{
    width: 3cm;
    padding: .1cm;
}

// skill name
.skill-name{
    font-size: 33px;
    letter-spacing: .025cm;
    color: rgba(197, 193, 193, 0.951);
    font-family: bree serif;
}

// skill proficiency
.proficiency{
    color: blueviolet;
    font-size: 18px;
}