// home svg file
.coder-svg {
    width: 10cm;
}

// home container primary
.Home{
    padding: 0vw 5vw;
}

// home container
.home-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 10vw;
    padding: 10vh 0vh;
}

// tagline home
.tagline {
    color: rgba(197, 193, 193, 0.951);
    // color: white;
    font-size: 70px;
    // font-weight: 600;
    letter-spacing: .05cm;
    font-family: bree serif;
    width: fit-content;
}
.tagline:hover{
    background-color: rgb(36, 115, 70);
}

// subscribe button
.subscribe{
    font-size: 19px;
    padding: .25cm;
    border: none;
    border-radius: 3px;
    cursor: pointer;
    // color: rgba(150, 175, 250, 0.921);
    background-color:rgba(162, 184, 251, 0.921);
}
.subscribe:hover{
    background-color:rgba(142, 170, 255, 0.921) ;
}
// sub tagline home
.sub-tagline {
    font-size: 35px;
    color: rgba(143, 171, 253, 0.921);
    font-family: inika;
    // font-weight: 600;
    letter-spacing: .02cm;
}

// know more button
.know-more {
    background-color: #77dd77;
    padding: 5px 10px;
    font-size: 18px;
    // font-weight: 600;
    border: none;
    border-radius: 3px;
    box-shadow: inset 0 0 0.5px 1px hsla(0, 0%,
            100%, 0.075),
        /* shadow ring ðŸ‘‡ */
        0 0 0 1px hsla(0, 0%, 0%, 0.05),
        /* multiple soft shadows ðŸ‘‡ */
        0 0.3px 0.4px hsla(0, 0%, 0%, 0.02),
        0 0.9px 1.5px hsla(0, 0%, 0%, 0.045),
        0 3.5px 6px hsla(0, 0%, 0%, 0.09);

    cursor: pointer;

}

.know-more:hover {
    color: black;
    background-color: #6bbd6b;
}

// general headings
.headingg {
    color: rgba(175, 175, 175, 0.848);
    // color: rgb(21, 138, 97);
    font-family: bree serif;
    letter-spacing: .025cm;
    font-size: 35px;
    text-decoration: underline;
}

// latest blog container home
.latest-blogs-container {
    padding-bottom: 10vh;
    padding-top: 2vh;
}

// latest blog details homepage
.blog-details-home {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 2vh 2vw;
    border-bottom: .5px solid rgb(54, 53, 53);

    
}
.blog-name-home {
    color: rgb(184, 181, 181);
    // color: rgba(197, 193, 193, 0.951);
    font-size: 21px;
    // font-weight: 600;
    letter-spacing: .02cm;
    font-family: roboto;
    cursor: pointer;
}
.news-tag{
    color: rgb(184, 181, 181);
    // color: rgba(197, 193, 193, 0.951);
    font-size: 20px;
    // font-weight: 600;
    letter-spacing: .02cm;
    font-family: roboto;
    
}
.blog-date-home {
    color: rgb(23, 146, 19);
    // font-size: 18px;
    font-weight: 600;
    letter-spacing: .03cm;
    font-family: roboto;
}
.blog-name-home:hover{
    color: rgb(135, 135, 252);
}
// latest blog name container homepage
.blog-name-container-home {
    display: flex;
    align-items: baseline;
    column-gap: .1cm;
}


// redirect icon
.redirect {
    width: .27cm;
}

// show more line
.show-more {
    padding-left: 2vw;
    cursor: pointer;
    color: rgb(148, 148, 255);
}

.show-more:hover {

    color: rgb(105, 105, 251);
    // text-decoration: underline;

}