// primary container
.About{
    padding: 10vh 5vw;
}

// About me division
.about-me{
    padding: 1cm;
    color: rgb(186, 183, 183);
    border: .5px solid rgb(41, 40, 40);
    font-family: roboto;
    font-size: 18px;
    background-color: #1e1e1e;
    // background-color: #12121abc;
    border-radius: 3px;
	box-shadow: rgba(122, 121, 121, 0.16) 0px 2px 3px, rgba(61, 60, 60, 0.23) 0px 2px 3px;

}

// primary about container
.about-me-container{
    padding: 5vh 0vw;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    column-gap: 2cm;
}

// click to go to skill page
.skill-click{
    color: rgba(110, 144, 245, 0.921);

}
.skill-click:hover{
    color: rgba(74, 116, 242, 0.921);
}

// my photo
.my-pic{
    width: 7cm;
    height: 7cm;
    border-radius: 500px;
    border: .5px solid rgb(41, 41, 41);
}